import React, {useState} from 'react';
import MyInput from "../input/MyInput";
import MyButton from "../button/MyButton";
import './Registration.css'
import {registrationAuth} from "../../../store/slices/auth";
import {useDispatch} from "react-redux";
import {useForm} from "react-hook-form";

const Registration = () => {

    const dispatch = useDispatch()

const {
        register,
        formState:{
            errors,
            isValid
        },
        handleSubmit,
        reset
} = useForm({
    mode:"onChange"
})
   const onSubmit = (data)=>{
       dispatch(registrationAuth(data))
       }

       return (
        <div className="registration">
            <div className='registrationContent'>
                <h1></h1>
                <form className='registration__form' onSubmit={handleSubmit(onSubmit)}>
                    <label> Username
                        <input
                            {...register('username',{
                            required: "   email",
                            minLength:{
                                value:5,
                                message: "     "},
                            pattern: {
                                 value: /\S+@\S+\.\S+/,
                                 message: " email"
                                    }
                                       }
                        )}
                        placeholder=' '
                        type="text" /></label>

                    <div className='registration__errors'>
                        {errors.username && <p style={{color:'red'}}>{errors.username.message || 'Error' }</p> }
                    </div>
                    <label>Password
                        <input
                            {...register('password',{
                                required: '   ',
                                minLength:{
                                    value:5,
                                    message:'    5 '
                                },
                                maxLength:{
                                    value:20,
                                    message:'    20 '
                                }
                            })}
                            placeholder=' '
                            type="password"/>
                    </label>
                <div className='registration__errors'>
                    {errors.password&& <p style={{color:'red'}}>{errors.password.message || 'Error'}</p>}
                </div>

                    <button disabled={!isValid} className='registration__btn' type='submit'></button>

                </form>
                  {/*<MyInput type='text' placeholder=' ' value={email} onChange={(e)=>setEmail(e.target.value)}/>*/}
                  {/*<MyInput type='text' placeholder=' ' value={password} onChange={(e)=>setPassword(e.target.value)}/>*/}
                  {/*<MyButton onClick = {regist}></MyButton>*/}
            </div>

        </div>
    );
};
export default Registration;