import React, {useEffect, useState} from 'react';
import axios from "../axios";


import styles from './Autos.module.scss'

const Autos = () => {

    const [autos,setAutos] = useState([
    ])


const getPhotos = async ()=>{
        const response = await axios ('/auth/auto')
        setAutos([...response.data])
     }
     useEffect(()=>{
         getPhotos()
     },[])
    console.log(autos)
    return (
        <div className={styles.wrapper}>
            <div className={styles.text}>
                <div className={styles.textOne}>  </div>
                <div className={styles.textTwo}>           </div>
            </div>
            {autos.map((obj,ind,_)=>
                <div id={obj._id} className=
                    { ind === 0||ind===2
                        ? styles.first
                        : styles.second
                      }
                    >
                    <div className={styles.autosImg}><img src={obj.photo} alt=""/></div>
                    <div className={styles.autosText}>
                        <p>: {obj.name} </p>
                        : <br/>
                        <ul>
                            <li> - {obj.length} cm. <br/></li>
                            <li> - {obj.height} cm. <br/></li>
                            <li> - {obj.width} cm. <br/></li>
                        </ul>
                         - {obj.weight} . <br/>
                    </div>
                </div>
            )}



            {/*      <h1 >  :</h1>*/}
      {/*<div><h3>                    .</h3></div>*/}
      {/*      <br/>*/}
      {/*      <div className='grid__wrapper__autos'>*/}
      {/*          {autos.map((auto)=>*/}
      {/*          <div key={auto._id} className='autos'><img src={auto.photo} width='300px' height='200px'/>*/}
      {/*              <ul>*/}
      {/*                  <li>: {auto.weight} </li>*/}
      {/*                  <li>: {auto.length}   </li>*/}
      {/*                  <li>: {auto.width} </li>*/}
      {/*                  <li>: {auto.height} </li>*/}
      {/*                  <li>: {auto.volume}..</li>*/}

      {/*              </ul>*/}

      {/*      </div>)}*/}
      {/*      </div>*/}
              </div>
    );
};

export default Autos;