import React, {useState} from 'react';
import './Registration.css'
import {useDispatch, useSelector} from "react-redux";
import {loginAuth} from "../../../store/slices/auth";
import {useForm} from "react-hook-form";
import './Login.css'


const Login = () => {
    // const [email,setEmail] = useState('')
    // const [password,setPassword] = useState('')
    const dispatch = useDispatch()
    // const isAuth = useSelector(state => state.users.isAuth)
    // const buttonLogin = ()=>{
    //     dispatch(loginAuth({username:email,password:password}))
    // }
    const {
        register,
        formState:{
            errors,
            isValid
        },
        handleSubmit,
        reset
       } = useForm({mode:'onBlur'})

       const onSubmit = (data)=>{
           dispatch(loginAuth(data))
       }
    return (
        <div className="authorization">
            <div className='authorizationContent'>
                <h1></h1>
                <form className='authorization__form' onSubmit={handleSubmit(onSubmit)}>

                    <label> <input type='text'
                       placeholder=' '
                       {...register('username',{
                         required: '  ',
                         pattern: {
                            value: /\S+@\S+\.\S+/,
                            message: " email"
                        }
                    })}/></label>
                    <div className='authorization__errors'>
                        {errors.username && <p>{errors.username.message || 'Errors'}</p>}
                    </div>
                    <label> <input type='password'
                       placeholder=' '
                       {...register('password',{
                           required: ' '
                       })}/></label>
                    <div className='authorization__errors'>
                        {errors.password && <p>{errors.password.message || 'Errors'}</p>}
                    </div>
                <button className='authorization__btn' type='submit'></button>
                </form>
            </div>

        </div>
    );
};

export default Login;