import React, {useState} from 'react';
import {Link, NavLink} from "react-router-dom";
import './NavbarJS.css'
import Dropdown from './Dropdown'

const NavbarJs = () => {
    const [click,setClick] = useState(false)
    const [dropdown,setDropdown]=useState(false)

    const handleClick = ()=>{setClick(!click)}
    const onMoueEnter = ()=>{setDropdown(true)}
    const onMouseLeave = ()=>{setDropdown(false)}

    return (
        <nav className='nav__wrapper'>
            <div
                onMouseEnter = {onMoueEnter}
                onMouseLeave = {onMouseLeave}
                onClick={()=>setDropdown(false)}
                className="nav__item nav__services">
                <NavLink className='nav__link' to = '/services'></NavLink>
                {dropdown&& <Dropdown/>}
            </div>
            <div className="nav__item nav__autos"><NavLink className='nav__link' to = '/autos'></NavLink></div>
            <div className="nav__item nav__calculator"><NavLink className='nav__link' to = '/calculator'></NavLink></div>
            <div className="nav__item nav__autos"><NavLink className='nav__link' to = '/about'> </NavLink></div>
        </nav>
        // <nav className='navbar'>
        //     <Link to='/' className='navbar__logo' onClick={handleClick}></Link>
        //
        //     <ul className={click ? 'navbar__menu active' : 'navbar__menu'}>
        //         <li onMouseEnter={onMoueEnter}
        //             onMouseLeave={onMouseLeave}
        //         className='navbar__items'>
        //             <Link to = '/services' className='navbar__links' ></Link>
        //             {dropdown && <Dropdown/> }
        //         </li>
        //         <li
        //             className='navbar__items'>
        //             <Link to = '/autos' className='navbar__links' ></Link>
        //         </li>
        //         <li
        //             className='navbar__items'>
        //             <Link to = '/calculator' className='navbar__links' ></Link>
        //         </li>
        //         <li
        //             className='navbar__items'>
        //             <Link to = '/about' className='navbar__links' > </Link>
        //         </li>
        //
        //     </ul>
        // </nav>
    );
};

export default NavbarJs;