import React, {useEffect} from 'react';
import axios from "../axios";
import {useSelector} from "react-redux";
import {useForm} from "react-hook-form";
import './UserPage.css'

const UserPage = () => {
    const user = useSelector(state => state.users.currentUser)
    const [avatar, setAvatar] = React.useState(null)

    useEffect(() => {
        (async () => {
            const response = await axios.post('/auth/avatar', {_id: user._id})
            setAvatar(response.data.message)
        })()
    }, [])

    const {
        register,
        formState:
            {
                errors,
                isValid
            },
        handleSubmit,
        reset
    } = useForm({mode: 'onBlur'})

    const onSubmit = (data) => {
        if (prompt('  ?' + JSON.stringify(data))) {
            reset()
        }
    }

    const changeInput = async (e) => {
        const formData = new FormData()
        formData.append('avatar', e.target.files[0])
        const {data} = await axios.post('/api/avatar', formData, {'content-type': 'multipart/form-data'})
        await axios.patch('/auth/change/' + user._id, {avatar:
                    // 'http://localhost:5000/avatar/'
                'https://point-to-point-backend.herokuapp.com/avatar/'
        +data.filename})
        const response = await axios.post('/auth/avatar', {_id: user._id})
        setAvatar(response.data.message)
    }
    return (
        <div className='userPage__wrapper'>
            <div className='userPage__wrapper__avatar'>
                <img src={avatar} className='userPage__img'/>

                <input type='file' className='userPage__p' onChange=
                    {changeInput}/>
            </div>
            <form className='userPage__form' onSubmit={handleSubmit(onSubmit)}>

                <label className='label__userPage__surname'> {errors.surname &&
                    <p style={{color: 'red'}}>{errors.surname.message || 'Error'}</p>}</label>
                <input className='userPage__surname' type='text' placeholder='...'
                       {...register('surname', {
                           required: '   '
                       })}/>

                <label className='label__userPage__name'> {errors.name &&
                    <p style={{color: 'red'}}>{errors.name.message || 'Error'}</p>}</label>
                <input className='userPage__name' type='text' placeholder='...'
                       {...register('name', {required: '   '})}
                />
                <label className='label__userPage__patronymic'>{errors.patronymic &&
                    <p style={{color: 'red'}}>{errors.patronymic.message || 'Error'}</p>}</label>
                <input className='userPage__patronymic' type='text' placeholder='...'
                       {...register('patronymic', {
                           required: '   '
                       })}
                />
                <label className='label__userPage__birthday'> {errors.date &&
                    <p style={{color: 'red'}}>{errors.date.message || 'Error'}</p>}</label>
                <input className='userPage__birthday' type='date' placeholder=' ...'
                       {...register('date', {
                           required: '   '
                       })}
                />
                <label className='label__userPage__email'> {errors.email &&
                    <p style={{color: 'red'}}>{errors.email.message || 'Error'}</p>}</label>
                <input className='userPage__email' type='email' placeholder=' ...'
                       {...register('email', {
                           required: '    ',
                           pattern: {
                               value: /\S+@\S+\.\S+/,
                               message: " email"
                           }
                       })}
                />
                <label className='label__userPage__phone'>{errors.tel &&
                    <p style={{color: 'red'}}>{errors.tel.message || 'Error'}</p>}</label>
                <input className='userPage__phone' type='tel' placeholder='...'
                       {...register('tel', {
                           required: '    '
                       })}
                />
                <label className='label__userPage__comments'></label>
                <textarea className='userPage__comments' placeholder='...'
                          {...register('comments')}
                />
                <button type='submit' className='userPage__btn'></button>
            </form>
        </div>
    );
};

export default UserPage;